import React, { useState, useEffect, useRef } from 'react'
import gsap from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

gsap.registerPlugin(ScrollTrigger)

const videos = [
  { id: 'video1', title: 'Clipe 1', thumb: '/assets/album1.jpg', src: 'https://www.youtube.com/embed/dQw4w9WgXcQ' },
  { id: 'video2', title: 'Clipe 2', thumb: '/assets/album2.jpg', src: 'https://www.youtube.com/embed/dQw4w9WgXcQ' }
]

export default function Videos(){
  const [open, setOpen] = useState(null)
  const sectionRef = useRef(null)
  const titleRef = useRef(null)
  const videoCardsRef = useRef([])

  useEffect(() => {
    const ctx = gsap.context(() => {
      // Animação do título
      if(titleRef.current) {
        gsap.fromTo(titleRef.current,
          { opacity: 0, y: 50, filter: 'blur(10px)' },
          {
            opacity: 1,
            y: 0,
            filter: 'blur(0px)',
            duration: 1,
            ease: 'power3.out',
            scrollTrigger: {
              trigger: titleRef.current,
              start: 'top 85%',
              toggleActions: 'play none none reverse'
            }
          }
        )
      }

      // Animação dos cards de vídeo
      videoCardsRef.current.forEach((card, idx) => {
        if(card) {
          gsap.fromTo(card,
            { opacity: 0, y: 60, scale: 0.9 },
            {
              opacity: 1,
              y: 0,
              scale: 1,
              duration: 0.8,
              ease: 'power3.out',
              delay: idx * 0.2,
              scrollTrigger: {
                trigger: card,
                start: 'top 85%',
                toggleActions: 'play none none reverse'
              }
            }
          )
        }
      })
    }, sectionRef)

    return () => ctx.revert()
  }, [])

  useEffect(() => {
    if(open) {
      // Animação de entrada do modal
      const modal = document.querySelector('.video-modal-content')
      if(modal) {
        gsap.fromTo(modal,
          { opacity: 0, scale: 0.8, filter: 'blur(20px)' },
          { opacity: 1, scale: 1, filter: 'blur(0px)', duration: 0.6, ease: 'power3.out' }
        )
      }
    }
  }, [open])

  useEffect(() => {
    function onKey(e) {
      if(!open) return
      if(e.key === 'Escape') setOpen(null)
    }
    window.addEventListener('keydown', onKey)
    return () => window.removeEventListener('keydown', onKey)
  }, [open])

  return (
    <section 
      id="videos" 
      ref={sectionRef}
      className="py-24 md:py-32 bg-gradient-to-b from-black via-bg-dark to-black grain-bg relative overflow-hidden"
    >
      {/* Background decorativo */}
      <div className="absolute inset-0 opacity-5">
        <div className="absolute top-1/2 right-0 w-96 h-96 bg-gold rounded-full blur-3xl"></div>
      </div>

      <div className="container relative z-10">
        <h2 
          ref={titleRef}
          className="text-4xl md:text-6xl lg:text-7xl font-heading text-gold mb-4 reveal"
        >
          Clipes e Performances
        </h2>
        <p className="text-white/60 text-lg mb-12 max-w-2xl">
          Momentos marcantes em vídeo da trajetória do grupo.
        </p>

        <div className="mt-12 grid grid-cols-1 md:grid-cols-2 gap-8 lg:gap-12">
          {videos.map((v, idx) => (
            <div 
              key={v.id}
              ref={el => videoCardsRef.current[idx] = el}
              className="relative video-thumb group reveal"
            >
              <img 
                src={v.thumb} 
                alt={v.title} 
                loading="lazy" 
                className="w-full h-64 md:h-80 object-cover rounded-xl" 
              />
              <div className="play-overlay">
                <div className="circle">
                  <svg className="w-10 h-10 text-white ml-1" fill="currentColor" viewBox="0 0 24 24">
                    <path d="M8 5v14l11-7z"/>
                  </svg>
                </div>
              </div>
              <div className="absolute bottom-0 left-0 right-0 p-6 bg-gradient-to-t from-black/90 via-black/50 to-transparent rounded-b-xl">
                <h3 className="text-white font-heading text-xl">{v.title}</h3>
              </div>
              <button 
                aria-label={`Abrir ${v.title}`} 
                onClick={() => setOpen(v)} 
                className="absolute inset-0"
                style={{background:'transparent'}}
              ></button>
            </div>
          ))}
        </div>
      </div>

      {/* Modal Premium */}
      {open && (
        <div 
          className="fixed inset-0 z-50 flex items-center justify-center video-modal-backdrop"
          onClick={(e) => {
            if(e.target.classList.contains('video-modal-backdrop')) setOpen(null)
          }}
        >
          <div className="absolute inset-0 modal-backdrop" onClick={() => setOpen(null)}></div>
          
          <div className="video-modal-content relative z-10 w-full max-w-6xl mx-4">
            <div className="relative aspect-video bg-black rounded-xl overflow-hidden shadow-2xl border border-gold/20">
              <iframe 
                src={open.src} 
                title={open.title} 
                className="w-full h-full" 
                frameBorder="0" 
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                allowFullScreen
              ></iframe>
            </div>
            
            {/* Botão fechar */}
            <button 
              aria-label="Fechar" 
              className="absolute -top-12 right-0 w-12 h-12 rounded-full glass-strong border border-gold/30 text-gold hover:bg-red/20 hover:border-red transition-all flex items-center justify-center"
              onClick={() => setOpen(null)}
            >
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          </div>
        </div>
      )}
    </section>
  )
}
